<template>
  <div class="sunmap">
    <h1>{{ msg }}</h1>
      <l-map ref="leafletMap" @moveend="recalc" style="height: 600px" :zoom="zoom" :center="center">
      <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
      <LHeatmap ref="heatmapLayer" :latLng=latLngArray :radius=radius :blur=blur :gradient=gradient :max=max></LHeatmap>
      </l-map>
  </div>
</template>

<script>
import { LMap, LTileLayer } from 'vue2-leaflet';
import LHeatmap from './Vue2LeafletHeatmap.vue';

const axios = require('axios');

export default {
  name: 'SunMap',
  props: {
    msg: String,
  },
  components: {
    LMap,
    LTileLayer,
    LHeatmap,
  },
  data() {
    return {
      map: null,
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution:
        '&copy; <a target="_blank" href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      zoom: 11,
      center: [50.50, 30.5],
      latLngArray: [],
      sunIntensities: {},
      max: null,
      radius: 100,
      blur: 30,
      gradient: { 0.1: 'green', 0.5: 'yellow', 1.0: 'orange' },
    };
  },
  created() {
    const lat = 50.0;
    const lng = 30.0;
    // this.$refs.refName.clientHeight
    // this.setCloudCover(lat, lng);
    const allPromises = [];
    for (let i = 0; i < 10; i += 1) {
      for (let j = 0; j < 10; j += 1) {
        allPromises.push(this.setCloudCover(lat + 0.05 * i, lng + 0.05 * j));
      }
    }
    console.log(this.sunIntensities);

    Promise.all(allPromises).then(() => {
      // results.forEach((response) => {
      //  console.log(response);
      // });
      const tmparr = [];
      console.log('sunintensities', JSON.stringify(Object.getOwnPropertyNames(this.sunIntensities)));
      Object.getOwnPropertyNames(this.sunIntensities).forEach((iLat) => {
        const lngObj = this.sunIntensities[iLat];
        console.log('lngobj', JSON.stringify(lngObj));
        Object.getOwnPropertyNames(lngObj).forEach((iLng) => {
          const sunnyness = lngObj[iLng];
          tmparr.push(iLat, iLng, sunnyness);
        });
      });

      // this.$refs.heatmapLayer.setLatLngs(tmparr);
      // this.$refs.heatmapLayer.redraw();
      console.log('tmparr', JSON.stringify(tmparr));
      // console.log('latlng', this.$refs.heatmapLayer.latLng);
    });
  },
  methods: {
    recalc(mapObject) {
      console.log('recalc');
      console.log(mapObject);
      const bnds = this.$refs.leafletMap.mapObject.getBounds();
      console.log(bnds);
      // this.latLngArray.push([50.45, 30.74, 80.80]);
      // this.$refs.heatmapLayer.addLatLng([50.55, 30.74, 80.80]);
    },
    setCloudCover(lat, lng) {
      console.log('setCloudCover');
      const config = {
        method: 'get',
        url: `http://api.weatherapi.com/v1/current.json?key=591b7934afcf484fa3191051223101&q=${lat},${lng}&aqi=no`,
        headers: { },
      };

      axios(config)
        .then((response) => (response.data))
        .then((data) => {
          // console.log(JSON.stringify(data));
          const sunnyness = 100 - data.current.cloud;
          if (!(lat in this.sunIntensities)) {
            this.sunIntensities[lat] = {};
          }
          this.sunIntensities[lat][lng] = sunnyness;
          // const datapoint = [lat, lng, sunnyness];
          // console.log(datapoint);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
